import React, { useEffect, useState } from 'react';
import { MapContainer, TileLayer, useMap, Marker, Popup } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet.heat';
import 'leaflet/dist/leaflet.css';

// Fix for default marker icons in Leaflet
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: '/leaflet/images/marker-icon-2x.png',
  iconUrl: '/leaflet/images/marker-icon.png',
  shadowUrl: '/leaflet/images/marker-shadow.png',
});

// Heatmap layer component
const HeatmapLayer = ({ data }) => {
  const map = useMap();
  const [heatLayer, setHeatLayer] = useState(null);

  useEffect(() => {
    if (!data || data.length === 0) return;

    // Convert data to heatmap format [lat, lng, intensity]
    const heatmapData = data
      .filter(item => item.lat && item.lon && item.pm25)
      .map(item => [
        parseFloat(item.lat),
        parseFloat(item.lon),
        parseFloat(item.pm25) / 10 // Normalize intensity
      ]);

    if (heatmapData.length === 0) return;

    // Remove previous heat layer if exists
    if (heatLayer) {
      map.removeLayer(heatLayer);
    }

    // Create new heat layer with current data
    const newHeatLayer = L.heatLayer(heatmapData, {
      radius: 25,
      blur: 15,
      maxZoom: 17,
      minOpacity: 0.5,
      gradient: {
        0.4: 'blue',
        0.6: 'cyan',
        0.7: 'lime',
        0.8: 'yellow',
        1.0: 'red'
      }
    }).addTo(map);

    setHeatLayer(newHeatLayer);

    // Fit map to heatmap bounds
    const bounds = L.latLngBounds(heatmapData.map(item => [item[0], item[1]]));
    map.fitBounds(bounds, { padding: [50, 50] });

  }, [data, map]);

  return null;
};

// Data point markers component
const DataMarkers = ({ data }) => {
  return (
    <>
      {data.map((item, index) => (
        <Marker 
          key={index} 
          position={[parseFloat(item.lat), parseFloat(item.lon)]}
        >
          <Popup>
            <div>
              <strong>PM2.5:</strong> {item.pm25} µg/m³<br />
              <strong>Temperature:</strong> {item.temp}°C<br />
              <strong>Humidity:</strong> {item.humidity}%<br />
              <strong>Wind:</strong> {item.wind} km/h<br />
              <small>{item.date} {item.time}</small>
            </div>
          </Popup>
        </Marker>
      ))}
    </>
  );
};

// Main heatmap component
const Heatmap = ({ data }) => {
  const [mapData, setMapData] = useState([]);

  // Process data when received
  useEffect(() => {
    if (data && data.length > 0) {
      const processed = data.map(item => ({
        ...item,
        lat: parseFloat(item.lat),
        lon: parseFloat(item.lon),
        pm25: parseFloat(item.pm25),
        temp: parseFloat(item.temp),
        humidity: parseFloat(item.humidity),
        wind: parseFloat(item.wind)
      }));
      setMapData(processed);
    }
  }, [data]);

  // Calculate center point from data
  const center = mapData.length > 0 
    ? [mapData[0].lat, mapData[0].lon]
    : [28.6139, 77.2090]; // Default to New Delhi coordinates

  return (
    <div style={{ height: '500px', width: '100%', borderRadius: '8px', overflow: 'hidden' }}>
      <MapContainer 
        center={center} 
        zoom={12} 
        style={{ height: '100%', width: '100%' }}
        scrollWheelZoom={true}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        <HeatmapLayer data={mapData} />
        <DataMarkers data={mapData} />
      </MapContainer>
    </div>
  );
};

export default Heatmap;