import React, { useState, useEffect } from "react";
import { Chart, registerables } from "chart.js";
import { Line } from "react-chartjs-2";
import axios from "axios";
import { 
  Container, Grid, Card, Typography, CircularProgress, 
  Button, ButtonGroup, Paper, Box, useTheme 
} from "@mui/material";
import { motion, AnimatePresence } from "framer-motion";
import LiveDataCards from "../components/LiveDataCards";
import LiveDataTable from "../components/LiveDataTable";
import StatusAlert from "../components/StatusAlert";
import Heatmap from "../components/Heatmap";
import Link from "next/link";

Chart.register(...registerables);

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000";
const MAX_POINTS = 30; // Increased data points for better trends

const Dashboard = () => {
  const [data, setData] = useState([]);
  const [chartData, setChartData] = useState({
    pm25: null,
    temp: null,
    humidity: null,
    wind: null
  });
  const [activeChart, setActiveChart] = useState("pm25");
  const [activeView, setActiveView] = useState("charts"); // 'charts' or 'map'
  const theme = useTheme();

  useEffect(() => {
    // Initial data load
    axios.get(API_URL)
      .then((response) => {
        const initialData = processData(response.data.slice(-MAX_POINTS));
        setData(initialData);
        updateCharts(initialData);
      });

    // WebSocket connection
    const socket = new WebSocket(WS_URL);
    
    socket.onmessage = (event) => {
      const newData = JSON.parse(event.data);
      if (typeof newData === "object" && newData.pm25 !== undefined) {
        setData(prevData => {
          const updatedData = processData([...prevData, newData].slice(-MAX_POINTS));
          updateCharts(updatedData);
          return updatedData;
        });
      }
    };

    return () => socket.close();
  }, []);

  const processData = (rawData) => {
    return rawData.map(item => ({
      ...item,
      lat: parseFloat(item.lat),
      lon: parseFloat(item.lon),
      pm25: parseFloat(item.pm25),
      temp: parseFloat(item.temp),
      humidity: parseFloat(item.humidity),
      wind: parseFloat(item.wind),
      timestamp: `${item.date} ${item.time}`
    }));
  };

  const updateCharts = (data) => {
    const timestamps = data.map((d) => d.time);
    setChartData({
      pm25: createChartData(timestamps, data.map((d) => d.pm25), "PM2.5 (µg/m³)", "#e63946"),
      temp: createChartData(timestamps, data.map((d) => d.temp), "Temperature (°C)", "#1d3557"),
      humidity: createChartData(timestamps, data.map((d) => d.humidity), "Humidity (%)", "#457b9d"),
      wind: createChartData(timestamps, data.map((d) => d.wind), "Wind Speed (km/h)", "#a8dadc")
    });
  };

  const createChartData = (labels, data, label, color) => ({
    labels,
    datasets: [{ 
      label, 
      data, 
      borderColor: color,
      backgroundColor: `${color}33`, // Add transparency
      borderWidth: 2,
      tension: 0.3,
      pointRadius: 3,
      fill: true
    }],
  });

  const currentData = data.length > 0 ? data[data.length - 1] : null;
  const aqiStatus = currentData?.pm25 > 100 ? "error" : 
                   currentData?.pm25 > 50 ? "warning" : "success";

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/* Header Section */}
      <Box sx={{ textAlign: "center", mb: 4 }}>
        <Typography 
          variant="h3" 
          fontWeight="bold" 
          sx={{ 
            color: "primary.main",
            mb: 1,
            background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
            WebkitBackgroundClip: "text",
            WebkitTextFillColor: "transparent"
          }}
        >
          Air Quality Monitoring Dashboard
        </Typography>
        <Typography variant="subtitle1" color="text.secondary">
          Real-time environmental data visualization
        </Typography>
      </Box>

      {/* View Toggle */}
      <Box sx={{ display: "flex", justifyContent: "center", mb: 3 }}>
        <ButtonGroup variant="contained">
          <Button 
            onClick={() => setActiveView("charts")} 
            color={activeView === "charts" ? "primary" : "inherit"}
          >
            Data Charts
          </Button>
          <Button 
            onClick={() => setActiveView("map")} 
            color={activeView === "map" ? "primary" : "inherit"}
          >
            Heatmap View
          </Button>
        </ButtonGroup>
      </Box>

      {/* Status Alert */}
      <Paper
        elevation={3}
        sx={{
          p: 3,
          mb: 4,
          borderRadius: 3,
          bgcolor: theme.palette[aqiStatus].main,
          color: "white",
          textAlign: "center",
          boxShadow: theme.shadows[4]
        }}
      >
        <StatusAlert pm25={currentData?.pm25 || 0} />
      </Paper>

      {/* Current Metrics */}
      <LiveDataCards data={currentData || {}} />

      {/* Main Content Area */}
      {activeView === "charts" ? (
        <>
          {/* Chart Selection */}
          <ButtonGroup fullWidth sx={{ my: 3 }}>
            {["pm25", "temp", "humidity", "wind"].map((metric) => (
              <Button
                key={metric}
                onClick={() => setActiveChart(metric)}
                variant={activeChart === metric ? "contained" : "outlined"}
                color="primary"
              >
                {chartData[metric]?.datasets[0].label}
              </Button>
            ))}
          </ButtonGroup>

          {/* Animated Chart */}
          <Card sx={{ 
            p: 3, 
            borderRadius: 3, 
            boxShadow: theme.shadows[3],
            minHeight: "400px"
          }}>
            <AnimatePresence mode="wait">
              <motion.div
                key={activeChart}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -20 }}
                transition={{ duration: 0.3 }}
              >
                {chartData[activeChart] ? (
                  <Line 
                    data={chartData[activeChart]} 
                    options={{
                      responsive: true,
                      plugins: {
                        legend: { display: false },
                        tooltip: {
                          backgroundColor: theme.palette.background.paper,
                          bodyColor: theme.palette.text.primary,
                          titleColor: theme.palette.primary.main,
                          borderColor: theme.palette.divider,
                          borderWidth: 1,
                          padding: 12,
                          boxShadow: theme.shadows[3]
                        }
                      },
                      scales: {
                        y: {
                          grid: { color: theme.palette.divider }
                        },
                        x: {
                          grid: { color: theme.palette.divider }
                        }
                      }
                    }}
                  />
                ) : (
                  <Box sx={{ display: "flex", justifyContent: "center", pt: 10 }}>
                    <CircularProgress />
                  </Box>
                )}
              </motion.div>
            </AnimatePresence>
          </Card>

          {/* Data Table */}
          <Box sx={{ mt: 4 }}>
            <LiveDataTable data={data} />
          </Box>
        </>
      ) : (
        /* Heatmap View */
        <Paper sx={{ 
          p: 2, 
          borderRadius: 3, 
          boxShadow: theme.shadows[3],
          height: "600px",
          overflow: "hidden"
        }}>
          <Heatmap data={data} />
        </Paper>
      )}
    </Container>
  );
};

export default Dashboard;